:host {
  position: absolute;
  z-index: z(overlay);
  box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
  top: 0;
  left: 0;
  right: 0;
  max-width: 100%;
  min-width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
  padding: 0 !important;
  border: none !important;
  novo-list {
    box-shadow: none;
  }
}